.backdrop {
  opacity: 1;
  background: rgba(0, 0, 0, .4);
  padding: 0 30px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.backdrop.modal-is-hidden {
  pointer-events: none;
  opacity: 0;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
}

body.modal-open {
  overflow: hidden;
}

.modal {
  opacity: 1;
  background: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  flex-direction: column;
  padding: 30px 28px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scaleX(1)scaleY(1);
}

.backdrop.modal-is-hidden .modal {
  transform: scaleX(.1)scaleY(.8);
}

